import React, { useEffect } from 'react';
import './ProductMap.scss';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'

const ProductMap = ({ productData }) => {

    const coordinates = [productData.latitude, productData.longitude];

    return (
        <div className="p-map-container">
            <h2>¿Dónde vas a estar?</h2>

            {/* <div className="p-map">
            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d255282.35853744816!2d36.707308253734915!3d-1.3028617907189453!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x182f1172d84d49a7%3A0xf7cf0254b297924c!2sNairobi%2C%20Kenia!5e0!3m2!1ses!2sar!4v1636481176278!5m2!1ses!2sar" allowfullscreen="" loading="lazy" className="map"></iframe>
            </div> */}

            <div className="map-container">
                <p>{productData.city.name}, {productData.city.country_name}</p>
                <MapContainer center={coordinates} zoom={13} scrollWheelZoom={false} id="map">
                    <TileLayer
                        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    />
                    <Marker position={coordinates}>
                        <Popup>
                            {productData.name}
                        </Popup>
                    </Marker>
                </MapContainer>
            </div>
        </div>
    )
};

export default ProductMap;