import Api from "./api";

const userService = {};

userService.postUser = (data) => {
    return Api.post("/users/", data)
    .then(res => res)
    .catch(err => {throw err})
}

userService.authUser = (data) => {
    return Api.post("/authenticate", data)
    .then(res => res)
    .catch(err => {throw err})
}


export default userService;