import Api from "./api";

const productService = {};

productService.getProducts = () => {
    return Api.get("/products/")
    .then(res => res.data)
    .catch(err => {throw err})
}

productService.postProducts = (data) => {
    return Api.post("/products/", data)
    .then(res => res.data)
    .catch(err => {throw err})
}

productService.getProductsById = (id) => {
    return Api.get(`/products/${id}`)
    .then(res => res.data)
    .catch(err => {throw err})
}

productService.updateProductsById = (id) => {
    return Api.put(`/products/${id}`)
    .then(res => res.data)
    .catch(err => {throw err})
}

productService.deleteProductsById = (id) => {
    return Api.delete(`/products/${id}`)
    .then(res => res.data)
    .catch(err => {throw err})
}

productService.getProductsByCategory = (id) => {
    return Api.get(`/products/categories/${id}`)
    .then(res => res.data)
    .catch(err => {throw err})
}

productService.getProductsByCity = (id) => {
    return Api.get(`/products/cities/${id}`)
    .then(res => res.data)
    .catch(err => {throw err})
}

productService.getRatedProducts = (rating) => {
    return Api.get(`/products/rating/${rating}`)
    .then(res => res.data)
    .catch(err => {throw err})
}

productService.getProductFilters = (city, dateStart, dateFinish) => {
    return Api.get(`/products/filter/${city}/${dateStart}/${dateFinish}`)
    .then(res => res.data)
    .catch(err => {throw err})
}

export default productService;