import React from 'react';
import './successfulBooking.scss';
import { useHistory, useLocation } from "react-router";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheckCircle } from '@fortawesome/free-solid-svg-icons';
import Layout from '../../components/Layout';
import Button from '../../components/Button';

const SuccessfulBooking = ({ }) => {
    const history = useHistory();
    const { productData } = useLocation();

    return (
        <Layout>
            <div className="main-container">
                <div className="successful-booking-container">
                    <iframe src="https://embed.lottiefiles.com/animation/46690"></iframe>
                    <h2>¡Muchas gracias!</h2>
                    <p>Su reserva para el alojamiento {productData?.name} se ha realizado con éxito</p>
                    <Button onClick={() => history.push("/")} className="card-button" variant="button-full" > Oki :D </Button>
                </div>
            </div>
        </Layout>
    );
};

export default SuccessfulBooking;