import React, { useState } from 'react';
import './home.scss';
import Categories from '../../components/Categories';
import Search from '../../components/Search';
import Layout from '../../components/Layout';
import List from '../../components/List';


const Home = () => {

    const [filterCategory, setFilterCategory] = useState("");
    const [filterCityDates, setFilterCityDates] = useState("");
    const [loading, setLoading] = useState(false);

    return (
        <Layout>
            <div className="main-home-container">
                <Search setFilterCityDates={(cityValue) => setFilterCityDates(cityValue)} loading={loading} />
                <Categories setFilterCategory={(categoryValue) => setFilterCategory(categoryValue)} loading={loading} />
                <List filterCategory={filterCategory} filterCityDates={filterCityDates} loading={loading} setLoading={(isLoading) => setLoading(isLoading)}/>
            </div>
        </Layout>
    );
};

export default Home;