import React, { useEffect, useState } from 'react';
import './List.scss';
import Card from '../Card';
import productsService from '../../services/products';

const List = ({ filterCityDates, filterCategory, setLoading, loading }) => {

    const [productsData, setProductsData] = useState([]);

    useEffect(() => {
        const getInfoData = async () => {
            try {
                setLoading(true)
                const resProducts = await productsService.getRatedProducts(6)
                if (resProducts) {
                    setProductsData(resProducts)
                }

            } catch (error) {
                console.log(error)
            } finally {
                setLoading(false)
            }
        }
        getInfoData()

    }, [])

    useEffect(() => {
        const getInfoData = async () => {
            try {
                setLoading(true)
                const resProducts = await productsService.getProductsByCategory(filterCategory)
                if (resProducts) {
                    setProductsData(resProducts)
                }

            } catch (error) {
                console.log(error)
            } finally {
                setLoading(false)
            }
        }
        if (filterCategory) {
            getInfoData()

        }

    }, [filterCategory])

    useEffect(() => {
        const getInfoData = async () => {
            try {
                setLoading(true)
                let resProducts = [];

                if(filterCityDates.dates.length == 0){
                    resProducts = await productsService.getProductsByCity(filterCityDates.city.cityId)
                }else{
                    resProducts = await productsService.getProductFilters(filterCityDates.city.cityId, filterCityDates.dates[0], filterCityDates.dates[1])
                }

                    setProductsData(resProducts)

            } catch (error) {
                console.log(error)
            } finally {
                setLoading(false)
            }
        }
        if (filterCityDates) {
            getInfoData()

        }

    }, [filterCityDates])

    return loading ? <iframe src="https://embed.lottiefiles.com/animation/9619"></iframe> : (
        <div className="list-title-container">
            {productsData ?
                <>
                    {filterCategory ?
                        <h3 className="list-title">{productsData[0].category.title}</h3>
                        : filterCityDates ? 
                        <h3 className="list-title">{filterCityDates.city.cityName}</h3>                        
                        :
                        <h3 className="list-title">Recomendaciones</h3>
                    }
                    <div className="list-container row">
                        {productsData.length ? productsData.map((product, index) => (
                            <Card productData={product} key={index} />
                        )) : <p>No se encontraron productos</p>}
                    </div>
                </> : null}
        </div>
    );
};

export default List;