import React, { useEffect, useState } from "react";
import "./search.scss";
import Calendar from "../Calendar";
import Button from "../Button";
import Select from 'react-select';
import citiesService from "../../services/cities";
import SelectOption from "../SelectOptions";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMapMarkerAlt } from "@fortawesome/free-solid-svg-icons";

const Search = ({ setFilterCityDates, loading }) => {

    const options = []
    const dates = []

    const [citiesData, setCitiesData] = useState([]);
    const [selectValue, setSelectValue] = useState([]);
    const [calendarValues, setCalendarValues] = useState([]);
    const [filterValues, setFilterValues] = useState([]);

    useEffect(() => {
        
        if (calendarValues) {
            {
                calendarValues.map((date, index) => (
                    dates[index] = date.format("YYYY-MM-DD")
                ))
            }
        }

        setFilterValues({
            city: selectValue,
            dates: dates
        })

    }, [calendarValues, selectValue])


    useEffect(() => {
        const getInfoData = async () => {
            try {
                const resCities = await citiesService.getCities();
                setCitiesData(resCities);

            } catch (err) {
                alert("Para recibir la información inicie el servidor en el puerto 8080");
                console.log(err)
            }
        }
        getInfoData();

    }, [])

    citiesData.map((location, index) => (
        options.push({
            value: location.id,
            label: <SelectOption ciudad={location.name} pais={location.country_name} key={index} />
        })
    ))

    const [value, setValue] = useState(null);

    const isSelected = (evento) => {
        setValue({
            value: evento.value,
            label:
                <div className="row" className="option-selected">
                    <FontAwesomeIcon icon={faMapMarkerAlt} className="option-icon" />
                    <p>{evento.label.props.ciudad}, {evento.label.props.pais}</p>
                </div>
        })

        setSelectValue({
            cityId: evento.value,
            cityName: evento.label.props.ciudad
        })
    }

    return (
        <div className="search-container">
            <div className="row">
                <h2>Busca ofertas en hoteles, casas y mucho más</h2>
            </div>
            <div className="form-container">
                <form className="form row">
                    <Select className="search-select"
                        placeholder={
                            <p>
                                <FontAwesomeIcon icon={faMapMarkerAlt} className="option-icon" />
                                ¿A dónde vamos?
                            </p>
                        }
                        options={options}
                        value={value}
                        onChange={isSelected}
                    />
                    <Calendar setCalendarValues={setCalendarValues} className="calendar" />
                </form>
                <Button onClick={() => !loading ? setFilterCityDates(filterValues) : null} variant="button-full" className="search-button">Buscar</Button>
            </div>
        </div>
    );
};

export default Search;