import React from 'react';

import './ProductPolicies.scss';

//Harcodeo este objeto que luego deberá venir desde el Back
const policies = {
    rules: {
        title: "Normas de la casa",
        description: [
            "Check-out 10:00hs",
            "No se permiten fiestas",
            "No fumar"
        ]
    },
    security: {
        title: "Salud y seguridad",
        description: [
            "Se aplican las pautas de distanciamiento social y otras normas relacionadas con el coronavirus",
            "Detector de humo",
            "Depósito de seguridad"
        ]
    },
    cancellation: {
        title: "Política de cancelación",
        description: [
            "Agregá las fechas de tu viaje para obtener los detalles de cancelación de esta estadía"
        ]
    }
}

const ProductPolicies = ({ productData }) => {

    //Cuando los datos se consuman desde el back descomentar esto y eliminar el objeto de arriba
    //const policies = productData.policies;

    return (
        <div className="policies-container">
            <h2>Qué tenés que saber</h2>
            <div className="policies-container-description">
                <div className="policies-description">
                    <h3>{policies.rules.title}</h3>
                    <ul>
                        {policies.rules.description.map((rule, index) =>
                            <li key={index}>{rule}</li>
                        )}
                    </ul>
                </div>
                <div className="policies-description">
                    <h3>{policies.security.title}</h3>
                    <ul>
                        {policies.security.description.map((rule, index) =>
                            <li key={index}>{rule}</li>
                        )}
                    </ul>
                </div>
                <div className="policies-description">
                    <h3>{policies.cancellation.title}</h3>
                    <ul>
                        {policies.cancellation.description.map((rule, index) =>
                            <li key={index}>{rule}</li>
                        )}
                    </ul>
                </div>

            </div>
        </div>
    );
};

export default ProductPolicies;